*,
*:before,
*:after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}

.header {
    width: 1920px;
    height: 108px;
    background-color: #aa0601;
    color: white;
    margin: 0 auto;
}

.header-logo {
    display: flex;
}

.logo {
    padding-right: 1140px;
    padding-left: 5.5rem;
}

.logo a {
    background: url(../images/logo.png);
    background-repeat: no-repeat;
    background-size: contain;
    text-indent: 101%;
    overflow: hidden;
    display: block;
    white-space: nowrap;
    width: 5rem;
    height: 3.04rem;
}

.header-logo h1 {
    float: left;
}

.header-menu {
    text-align: center;
}

.menu-item {
    padding: 0 2rem;
    font-size: 1.25rem;
    line-height: 1.875rem;
    text-align: center;
}

.menu-item a {
    text-decoration: none;
    color: white;
    display: block;
    padding: 10px 0px;
}

.footer {
    width: 1920px;
    height: 332px;
    background-color: #aa0601;
    margin: 0 auto;
    padding-left: 405px;
}

.footer-menu {
    font-size: 1rem;
    line-height: 1.5;
    text-align: center;
}

footer li a {
    text-decoration: none;
    color: white;
}

.menu-contact {
    padding-top: 2.75rem;
}

.media-icon {
    padding-top: 3rem;
    padding-left: 390px;
}

.media-icon li {
    padding-right: 2rem;
}

.contact {
    font-size: 2.25rem;
    line-height: 1.5;
}

.contact img {
    padding-right: 1.25rem;
}

.contact-tele {
    padding-bottom: 1rem;
}

.contact-email {
    padding-bottom: 1.5rem;
    border-bottom: 1px solid white;
}

.private {
    font-size: 1rem;
    line-height: 1.5;
    color: white;
    padding-top: 2rem;
    padding-bottom: 39px;
}

.private-list1 {
    padding-right: 38.375rem;
}